<template>
<div class="d-flex flex-column text-input-wrapper" :class="wrapperClass">
    <label class="text-input-label" :class="hasLabel ? 'label-take-place' : ''">{{label}}<span class="text-danger">{{isRequired ? ' *':''}}</span></label>
    <input autocomplete="off" :type="`${isPassword ? 'password' : 'text'}`" class="text-input" :placeholder="placeholder" :value="value" @change="changeLocal" :disabled="isDisabled" :name="name" :maxlength="maxLength">
    <div class="text-danger error"></div>
</div>
</template>

<script>
export default {
    name: 'TextInput',
    props: {
        label: String,
        value: String,
        type: String,
        error: String,
        wrapperClass: String,
        placeholder: {
            type: String,
            default: ''
        },
        name: {
            type: String,
            required: true,
        },
        isPassword: {
            type: Boolean,
            default: false
        },
        isRequired: {
            type: Boolean,
        },
        isDisabled: {
            type: Boolean,
        },
        hasLabel: {
            type: Boolean,
        },
        maxLength: {
            type: Number,
            default: 255,
        }
    },
    model: {
        event: 'change'
    },
    methods: {
        changeLocal: function (event) {
            this.$emit('change', event.target.value);
        }
    },
    computed: {
        // errorMessage: function () {
        //     let item  = this.errors.items.find(item => item.field === this.name);
        //     if (item) {
        //         return item.msg;
        //     } else {
        //         return '';
        //     }
        // }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/variables.scss';

.text-input-wrapper {
    .text-input-label {
        font-family: NotoSansKR;
        font-weight: 300;
        font-size: 1.4rem;
        line-height: 2rem;
        color: #7C848D;
        margin-bottom: .7rem;

        &.label-take-place {
            min-height: 2.0rem;
        }
    }

    .text-input {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: normal;
        font-size: 1.4rem;
        line-height: 2.0rem;
        color: #4f4f4f;
        outline: none;
        border: none;
        border-bottom: 0.05rem solid #E0E0E0;
        padding-bottom: .5rem;

        &:focus {
            color: $cy-blue;
            border-bottom: 0.05rem solid $cy-blue;
        }

        &::placeholder {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: #4F4F4F;
            opacity: 0.5;
        }
    }

    .error {
        display: inline-block;
        font-size: 1.2rem;
        line-height: 2.0rem;
        width: 100%;
        margin-bottom: .3rem;
        min-height: 2.0rem;
        position: relative;
    }
}
</style>
