<template>
<div class="change-pwd-form">
    <div>
        <TextInput v-model="current_pwd" isRequired :label="$t('Common.ChangePwd.CurrentPwd')" :wrapperClass="'w-100'" name="current password" :isPassword="true" v-validate="'required|min:8'" />
    </div>
    <div>
        <TextInput v-model="new_pwd" isRequired :label="$t('Common.ChangePwd.NewPwd')" :wrapperClass="'w-100'" name="new password" :placeHolder="$t('Common.ChangePwd.Require')" :isPassword="true" v-validate="'required|min:8'" />
    </div>
    <div>
        <TextInput v-model="confirm_pwd" isRequired :label="$t('Common.ChangePwd.ConfirmPwd')" :wrapperClass="'w-100'" name="password confirm" :placeHolder="$t('Common.ChangePwd.Require')" :isPassword="true" v-validate="'required|min:8'" />
    </div>
    <div class="row m-0 save">
        <Button :label="$t('Common.ChangePwd.Button')" :wrapperClass="'w-100'" @click.native="changePwd"></Button>
    </div>
</div>
</template>

<script>
import Button from '@/components/Common/Button/Button'
import TextInput from '@/components/Common/Input/TextInput'
import {
    SHOW_POPUP_BUTTON,
    HIDE_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    CHANGE_PASSWORD
} from '@/mutations/Auth.mutation'

export default {
    name: 'ChangePassword',
    components: {
        TextInput,
        Button
    },
    data: function () {
        return {
            current_pwd: null,
            new_pwd: null,
            confirm_pwd: null,
        }
    },
    methods: {
        async changePwd() {
            try {
                let isValid = await this.$validator.validateAll();
                if (this.new_pwd !== this.confirm_pwd) {
                    throw new Error(this.$t('Joining.Passwordsdonotmatch'))
                }

                if (!isValid) {
                    let error = this.errors.items[0];
                    this.$store.dispatch(SHOW_POPUP_BUTTON, error.msg);
                } else {
                    let user = localStorage.getItem('user');
                    let data = {
                        userId: JSON.parse(user).userId,
                        currentPw: this.current_pwd,
                        changePw: this.new_pwd,
                    }
                    await this.$store.dispatch(CHANGE_PASSWORD, data);
                }
            } catch (err) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, err.message);
            }
        }
    }
}
</script>

<style lang="scss">
.change-pwd-form {
    margin: 2rem;

    input[type='password'] {
        font-size: 2rem !important;
    }

    .save {
        padding-top: 3rem;

        .cy-btn {
            &:active:focus {
                outline: none;
                box-shadow: none;
            }
        }
    }
}
</style>
